@-webkit-keyframes :local(fadeInHeader) {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes :local(fadeInHeader) {
  0% {
    opacity: 0;
  }

  20% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

:local .Header :local {
  padding: 2em 0 5em;
  text-align: center;
  -webkit-animation: fadeInHeader 1s;
          animation: fadeInHeader 1s;
}

@-webkit-keyframes :local(fadeInNav) {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(4em, 0, 0);
            transform: translate3d(4em, 0, 0);
  }

  50% {
    opacity: 0;
    -webkit-transform: translate3d(4em, 0, 0);
            transform: translate3d(4em, 0, 0);
  }

  100% {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}

@keyframes :local(fadeInNav) {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(4em, 0, 0);
            transform: translate3d(4em, 0, 0);
  }

  50% {
    opacity: 0;
    -webkit-transform: translate3d(4em, 0, 0);
            transform: translate3d(4em, 0, 0);
  }

  100% {
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}

:local .nav :local {
  -webkit-animation: fadeInNav 2s;
          animation: fadeInNav 2s;
}

:local .navList {
  margin: 5em 0 0;
  padding: 0;
  list-style-type: none;
}

:local .navList li {
  margin: 0;
  padding: 0;
  display: block;
}

:local .navList a {
  padding: 1em 0.5em 1em 0;
  display: block;
}

:local .icon {
  margin-right: 0.5em;
}


/*
  BREAKPOINT: Phablet
*/
@media (min-width: 580px) {
  :local .Header {
    padding: 0 0 5em;
    text-align: left;
  }

  :local .nav li {
    display: inline-block;
    padding-right: 2em;
  }
}

/*
  BREAKPOINT: Desktop
*/
@media (min-width: 1280px) {
  :local .Header {
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 0 1em 0 0;
  }

  :local .nav li {
    display: block;
  }
}



