@-webkit-keyframes :local(fadeInBody) {
  0% {
    opacity: 0;
  }

  66% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes :local(fadeInBody) {
  0% {
    opacity: 0;
  }

  66% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

:local .Body :local {
  -webkit-animation: fadeInBody 3s;
          animation: fadeInBody 3s;
}

:local .workItem {
  margin-bottom: 2em;
}

:local .workItemIconContainer {
  display: inline-block;
  width: 4em;
  vertical-align: top;
}

:local .icon {
  font-size: 2em;
  margin-top: -0.2em;
}

:local .workItemTitle {
  display: inline-block;
  font-size: 1.25em;
  margin: 0.2em 0;
  width: calc(100% - 4em);
}

:local .workItemDescription {
  width: 100%;
  padding: 0.4em;
}

@media (min-width: 580px) {
  :local .workItemDescription {
    padding: 0 0 0 4em;
  }
}

/*
  BREAKPOINT: Desktop
*/
@media (min-width: 1280px) {
  :local .Body {
    -ms-flex: 2 1;
        flex: 2 1;
    padding: 1em 0 0 7em;
    border-left: 1px solid #ddd;
  }
}



